<template>
<div id="app" v-if="ready">
	<section class="section-rotate">		
		<div class="section-inner bg-gradient-primary"></div>	
	</section>
		<div class="p-5 bg-gradient-primary">
		<div class="container">	
			<div class="row justify-content-between">
                    <div class="col-lg-5 text-center text-lg-left">
                        <div class="">
                            <h2 class="text-white my-4">
	                    <span class="d-block">Shipping calculation <br><strong class="font-weight-light">Made Simple</strong></span>
	                </h2>
                            <p class="lead text-white">Save time and money with the #1 cloud-based shipping software. Our 35,000+ customers use Project to get rates, track, and print labels for millions of packages per month.</p>
                            <div class="mt-5 hide">
                                <a href="#sct_page_examples" class="btn btn-dark btn-circle btn-translate--hover btn-icon mr-sm-4 scroll-me">
                                    <span class="btn-inner--text">Page Examples</span>
                                    <span class="btn-inner--icon"><i class="fas fa-angle-right"></i></span>
                                </a>
                            </div>
                        </div>
                    </div>					
                    <div class="col-lg-6 text-center text-lg-right text-white mt-4" id="search_box">
						<div class="form-group" >
							<div class="row">
								<div class="col">									
									<div class="row justify-content-between">
										<div class="col text-left  pr-0"><label for="formControlRange">Ship from</label></div>
										<div class="col text-right pl-0"><span id="origin_loc" class="loc_name">{{ ori_zip }}</span></div>
									</div>
									<div class="input-group">
										<div class="input-group-prepend">
										  <div class="input-group-text"><span v-bind:class="'flag-icon flag-icon-us'"></span></div>
										</div>									
										<input type="text" class="form-control" id="f_o" placeholder="Place or Postal code" v-model="origin" v-on:keyup="find_orig()">
										<div id="orig_list" class="cn_list dropdown-menu">
										  <a class="dropdown-item" href="#/" v-for="i in orig_list" v-on:click="set_orig(i)">{{ i.postal_code }} - {{ i.place_name_string }}, {{ i.state }}, {{ i.country_code }}</a>
										</div>
									 </div>
								</div>
								<div class="col">
									<div class="row justify-content-between">
										<div class="col text-left  pr-0"><label for="formControlRange">Destination</label></div>
										<div class="col text-right pl-0"><span id="dest_loc" class="loc_name">{{ des_zip }}</span></div>
									</div>
									<div class="input-group">
										<div class="input-group-prepend">
										  <div class="input-group-text"><span v-bind:class="'flag-icon flag-icon-'+this.des_cn"></span></div>
										</div>									
										<input type="text" class="form-control" id="f_d" placeholder="Place or Postal code" v-model="destination" v-on:keyup="find_dest()">
										<div id="dest_list" class="cn_list dropdown-menu">
										  <a class="dropdown-item" href="#/" v-for="i in dest_list" v-on:click="set_dest(i)">{{ i.postal_code }} - {{ i.place_name_string }}, {{ i.state }}, {{ i.country_code }}</a>
										</div>
									 </div>
								</div>
							</div>
						</div>
						<div class="form-group row text-left">
							<div class="col-sm-9">
								<label for="formControlRange">Package Weight</label>
								<input type="range" class="form-control-range" id="formControlRange"  v-model="weight">
							</div>
							<div class="col-sm-3">
								<div class="input-group mt-2">
									<input type="text" class="form-control" id="f_w" value="1.5" v-model="weight">
									
									<div class="input-group-append">
									  <div class="input-group-text">lbs</div>
									</div>
								 </div>
							</div>
						</div>
						<div class="form-group row">
							<div class="col-sm-3 text-left">
								<label  class="mb-0">Dimension</label>
								<div class="row mb-2">
								<div class="col ml-4 ">
									<input class="form-check-input" type="radio" id="unit_inch" name="unit" value="inch" v-model="unit" checked>
									<label class="form-check-label" for="unit_inch">inch</label>
								</div>
								<div class="col ">
									<input class="form-check-input" type="radio" id="unit_cm" name="unit" value="cm" v-model="unit">
									<label class="form-check-label" for="unit_cm">cm</label>
								</div>
								</div>
							</div>
							<div class="col-sm-3">
								<div class="input-group mb-2">
									<input type="text" class="form-control" id="f_pl" placeholder="Length" v-model="pl">		
								 </div>
							</div>
							<div class="col-sm-3">
								<div class="input-group mb-2">
									<input type="text" class="form-control" id="f_pw" placeholder="Width" v-model="pw">		
								 </div>
							</div>							
							<div class="col-sm-3">
								<div class="input-group mb-2">
									<input type="text" class="form-control" id="f_ph" placeholder="Height" v-model="ph">		
								 </div>
							</div>
						</div>
						  <button class="btn btn-success" type="submit" v-on:click="calc()">Calculate</button>
					</div>
				</div>
			</div>
		</div>
</div>
<div id="busy" v-else>
		<busy-screen></busy-screen>
</div>
</template>

<script>
import axios from 'axios';
import WinBusy from '@/components/Busy'

export default {
  name: 'Home',
  components:{
	'busy-screen':WinBusy
  },
  props:['sign'],
  data () {
    return {
		ready: false,
		url: this.$parent.url,
		origin: '',
		coords: '',
		orig_list: [],
		dest_list: [],
		ori_zip: '',
		ori_cn: '',
		des_cn: '',
		des_zip: '',
		destination: '',
		unit: 'inch',
		weight: '1',
		pl:'',
		ph:'',
		pw:''
	}
  },
  watch:{
	'origin'(to,from){
		
	},
	'destination'(to,from){
		
	}
  },
  created: function() {
	this.$parent.current('home');
  },
  mounted: function() {
	this.ready=true;
  },
  methods: {			
		find_orig: function(){	
			var to=this.origin;
			this.ori_cn=this.ori_zip=this.coords='';
			if(to.length>3){
				axios.get('https://ezcmd.com/apps/api_geo_postal_codes/search/832b62f1efe4928b85b74238562b4929/389?zip_query='+to+' usa')
				.then(r =>{ 
					$('#orig_list').show();
					if(r.data.success){
						this.orig_list=r.data.search_results.splice(0,5);
						
					} else{
						$('#orig_list').hide();
					}
				}).catch(e =>{
				
				});
			}
		},
		find_dest: function(data){
			var to=this.destination;
			this.des_cn=this.des_zip='';
			if(to.length>3){
				axios.get('https://ezcmd.com/apps/api_geo_postal_codes/search/832b62f1efe4928b85b74238562b4929/389?zip_query='+to)
				.then(r =>{ 
					$('#dest_list').show();
					if(r.data.success){
						this.dest_list=r.data.search_results.splice(0,5);
					} else{
						$('#dest_list').hide();
						
					}
				}).catch(e =>{
				
				});
			}
		},
		set_orig: function(data){		
			this.ori_cn=data.country_code.toLowerCase();
			this.coords=data.coords;
			this.ori_zip=data.postal_code;
			this.o_place=data.place_name_string+', '+data.state+', '+data.country_code;
			this.origin=this.o_place;
			$('#orig_list').hide();
			this.orig_list=[];						
		},
		set_dest: function(data){
			this.des_cn=data.country_code.toLowerCase();
			this.des_zip=data.postal_code;
			this.d_place=data.place_name_string+', '+data.state+', '+data.country_code;
			this.destination=this.d_place;
			$('#dest_list').hide();
			this.dest_list=[];
		},
		calc: function(){	
			var err=0;
			$('#search_box input').removeClass('is-invalid');
			if(this.pl<0.1){
				$('input#f_pl').addClass('is-invalid');
				err++;
			}
			if(this.ph<0.1){
				$('input#f_ph').addClass('is-invalid');
				err++;
			}
			if(this.pw<0.1){
				$('input#f_pw').addClass('is-invalid');
				err++;
			}
			if(this.weight<0.1){
				$('input#f_w').addClass('is-invalid');
				err++;
			}
			
			if(this.ori_zip==''){
				$('input#f_o').addClass('is-invalid');
				err++;
			}
			
			if(this.des_zip==''){
				$('input#f_d').addClass('is-invalid');
				err++;
			}
			if(err==0){
				//localStorage.origin=$('#origin_loc').text();
				//localStorage.destination=$('#dest_loc').text();
				localStorage.origin=this.origin;
				localStorage.destination=this.destination;
				localStorage.weight=this.weight;
				localStorage.unit=this.unit;
				localStorage.coords=this.coords;
				localStorage.ori_zip=this.ori_zip;
				localStorage.des_zip=this.des_zip;
				localStorage.pl=this.pl;
				localStorage.pw=this.pw;
				localStorage.ph=this.ph; 
				window.location.hash="#/calculate";	
			}
		}
	}
}


</script>

<style scoped>
.section-rotate{overflow-x:hidden;  position: absolute;
    z-index: -2;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;}
	
.section-rotate .section-inner {
  
}
.section-rotate .section-inner {
	  position: relative;
    top: -10%;
    left: -1rem;
    width: 130%;
    height: 90%;
    transform: rotate(-4deg);
    border-bottom-left-radius: 3rem;
}
@media (min-width: 992px)
.section-rotate .section-inner {
    overflow: hidden;
    background: inherit;
}
.bg-gradient-primary {
    background: linear-gradient(80deg,#510fa8 0,#320fa8 100%)!important;
}
</style>



